import { CreatG } from '@/api/index'
import { setTimeout } from 'timers';
export default {
  data () {
    return {
      crowd: '',
      headName:'创建群组',
      show:false, 
      mask:false,
      busy:false
    }
  },
  methods: {
    joinG() {
        if(!this.crowd.trim()) {
             this.show = true
           return setTimeout(() => {
              this.show = false
            }, 1500);
        } else {
             CreatG({groName:this.crowd.trim()}).then( res => {
                if(res.code == 1) {
                   return   this.$router.push({path: '/nav/group'})
                } else if (res.code == 403 ) {
                   this.mask = true
                   return setTimeout(() => {
                         this.mask = false
                       }, 1500);
                } else {
                    this.busy = true
                   return setTimeout(() => {
                         this.busy = false
                       }, 1500);
                }
              })
        }  
    }
  },
  components: {
  }
}



