
import util from '../util/util'
import { recordBook } from "@/api/index"
import dateFormat from 'vux/src/tools/date/format.js'

import { Toast } from 'vant'
export default {
data () {
return {




tiems: '',
count: 0,
begin: '',
end: 1,
number: 1,
alls: 0,
values: '',
first: [{title: '六项精进大纲', readCount: 3, readTotal: 0}, {title: '大学开篇', readCount: 3, readTotal: 0}],
pass: true,
pickerValue: '',
old: 0,
Number: true,
mask: true,
layout: false,
text1: false,
text2: false,
text3: false,
dateObj: {
dateMask: false,
dateMasks: false
},
newTime: '',
begins: ''
}
},
computed:{

},
methods: {
skipFn(){
this.record(true);
},
texte() {
console.log(event.type)
},
dateConfirm(type,value){
if (type === 'year') {
return `${value}年`;
} else if (type === 'month') {
return `${value}月`
} else {
return `${value}日`
}
return value;
},
datePicker () {
this.dateObj.dateMask = !this.dateObj.dateMask
},
datePickers() {
this.dateObj.dateMasks = !this.dateObj.dateMasks
},
confirms (val) {

this.begin = dateFormat(val, 'YYYY-MM-DD')
},
confirm (val) {
var s1 = new Date(val);
var s2 = new Date();
var days = s2.getTime() - s1.getTime();
this.count = parseInt(days / (1000 * 60 * 60 * 24))
this.old = parseInt(days / (1000 * 60 * 60 * 24))
this.tiems = dateFormat(val, 'YYYY-MM-DD')
},
addbook () {
this.first.push({title: '', readCount: 1, readTotal:0})
},
delbook (i) {
this.first.splice(i,1)
},
bblur () {
window.scroll(0,0);
},
lose() {

},
bid () {
document.body.scrollTop= 0;
},
getTime () {
this.tiems = dateFormat(new Date(), 'YYYY-MM-DD')
this.newTime = new Date();
this.begins =  new Date()
this.begin = dateFormat(new Date(), 'YYYY-MM-DD')
},
func (index) {
if (index == 0) {
return false
} else {
return true
}
},
sever (val) {
if( val ) {
return true
} else {
return false
}
},
text () {
if(this.count >= this.old) {
this.count = this.old
}
},
textM () {
var myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/
if( myreg.test(this.mobile)) {
this.$refs.Moblie.style.color = '#000'
window.scroll(0,0)
this.Number = true
} else {
this.$refs.Moblie.style.color = 'red'
this.Number = false
this.layout = true
this.verify()
window.scroll(0,0)
}
},
verify() {
setTimeout(() => {
this.layout = false
}, 1500);
},
record (a) {
this.pass = true;
if( !this.Number ) {
return  this.layout = true
this.verify()
}















this.first.forEach( el => {
var arr = Object.keys(el)
if(  arr.length != 3 ) {
this.pass = false;
this.text3 = true
return setTimeout(() => {
this.text3 = false
}, 1500);
}
});
this.loading()
let data={
pushStart: this.tiems,
total: this.count,
bonaStart: this.begin,
bonaEnd: this.end,
bonaCount: this.number,
bonaTotal: this.alls,
volunteer: this.values,
books: this.first
}
if(a||a=='true'){
data.isJump=1
}
if ( this.pass  ) {
recordBook(data).then( res => {
if(res.code == 1) {
this.hideLoading()

this.$router.replace({path:'/nav/card'})
}
})
}

},
openPicker() {
this.$refs.picker.open();
},
},
watch:{
pickerValue(val) {
this.tiems = util.itcTiem(this.pickerValue)
}
},
created () {
this.getTime()
},
components: {
},
mounted() {









},
beforeDestroy() {









},

}



