  import { CardRecord,groupUser,groups } from '@/api/index';
  import { Toast } from 'mint-ui';
  export default {
    data () {
      return {
        groupName:'',//群名
        type:'',
        list:[],
        groId:'',//群id
        img:'',//群头像
        mask:false,
        maskToast:false,
        toastTxt:'',
        thisNum:'',
        tipTxt:'',
        pageList: {total: '',pageNo:1,pageSize:8},
        keywords:'',
        isScroll:true,
        noGet:true,
        text3: false,
        text2: false,
        btnTxt:'解散群组',
        isClick1:true,
        isClick2:true,
      }
    },
    name:'groupUser',
    watch:{

    },
    methods: {
      //跳到二维码
      jumpQrCode(){
        this.$router.push({
          name:'groupQrcode',
          query:{
            id:this.groId,
            name:this.groupName,
          }
        })
      },
      //跳到打卡圈
      jumpFriend(i){
        this.$router.push({
          path:'/nav/friend',
          query:{
            id:this.list[i].pkid
          }
        })
      },
      //确定
      maskFn(){
        let i=this.thisNum;
        if(this.tipTxt=='确认要删除该家人？'){
          let data={
            groId:this.groId,
            user:this.list[i].pkid
          }
          this.list.splice(i,1);
          groupUser.removeUser(data).then(res =>{
            this.isClick1=true;
            if(res.code==1){
              this.text3 = true
              setTimeout(() => {
                this.text3 = false
              }, 1500);
              this.list=[];
              this.ajax();
            }
          })
        }else if(this.tipTxt=='确认要转让该群组？'){
          let data={
            groId:this.groId,
            invite:this.list[i].pkid
          }
          groupUser.changeGrouper(data).then(res =>{
            this.isClick2=true;
            if(res.code==1){
              this.text2 = true
              this.$router.replace({
                path:'/nav/group',
              })
            }else{
              this.toastTxt=res.msg;
              this.maskToast=true;
              setTimeout(() => {
                this.maskToast = false;
                location.reload();
              }, 1000);
            }
          })
        }else{
          let data={
            groId:this.groId,
            type:''
          }
          if(this.tipTxt=='确认解散该群？'){//解散
            data.type='dissolve'
          }else{//退出
              data.type='exit'
          }
          groupUser.operateGrouper(data).then(res =>{
            if(res.code==1){
              this.$router.replace({
                path:'/nav/group',
              })
            }
          })
        } 
      },
      deleteFn(i){
        if(!this.isClick1){
          return false
        }
        this.isClick1=false;
        this.thisNum=i;
        this.tipTxt='确认要删除该家人？'
        this.mask=true;
      },
      turnFn(i){
        if(!this.isClick2){
          return false
        }
        this.isClick2=false;
        this.thisNum=i;
        this.tipTxt='确认要转让该群组？';
        this.mask=true;
      },
      btnFn(){
        if(this.btnTxt=='解散该群'){
          this.tipTxt='确认解散该群？'
        }else{
          this.tipTxt='确认退出该群？'
        }
        this.mask=true;
      },
      ajax(type){
        // this.loading();
        if(type=='search'){
          this.pageList.pageNo=1;
          this.noGet=false;
        }
        let that=this;
        let data={
          pageNo:this.pageList.pageNo,
          pageSize:this.pageList.pageSize,
          groId:this.$route.query.id,//
          keywords:this.keywords,
        }
        CardRecord.groupPerson(data).then(res =>{
          // this.hideLoading();
		      this.isScroll=true;
          if(res){
            if(that.pageList.pageNo>1){
              that.list = that.list.concat(res.data.list);
              that.pageList.total = res.data.total;
            }else{
              that.list = res.data.list;
              that.pageList.total = res.data.total;
            }
            if(res.data.total==that.list.length||that.list.length<that.pageList.pageSize){
              that.noGet=true;//如果返回总条数等于当前list长度
            }else{
              that.noGet=false
            }
          }
        })
      },
      sh(){
        let bodyScrollHeight = 0
        let documentScrollHeight = 0
        if (document.body) {
          bodyScrollHeight = document.body.scrollHeight
        }
        if (document.documentElement) {
          documentScrollHeight = document.documentElement.scrollHeight
        }
        return (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight
      },
      //监听滚轮
      scrollgun(){
        let getSt=document.documentElement.scrollTop || document.body.scrollTop,
          getWh=document.documentElement.clientHeight || document.body.clientHeight;
        let scrollBottom=this.sh()-(getSt+getWh),//滚动条距离底部距离
          rem=parseInt(window.getComputedStyle(document.documentElement)["fontSize"]),
          h=100;
        if(this.noGet){
          return false
        }
        if(!this.isScroll){
          return false
        }
        if(scrollBottom<h){
          this.isScroll=false;
          this.pageList.pageNo+=1;
          this.ajax();
        }
      },
    },
    created () {
      this.groupName=this.$route.query.name;
      this.type=this.$route.query.type;
      this.groId=this.$route.query.id;
      this.img=this.$route.params.img;
      if(this.type==0){
        this.btnTxt='解散该群'
      }else{
        this.btnTxt='退出该群'
      }
      this.ajax();
    },
    components: {

    },
    mounted(){
      window.addEventListener('scroll',this.scrollgun,true);
    },
    destroyed(){
      window.removeEventListener('scroll',this.scrollgun,true);
      groups({}).then( res => {
        let arr=[];
        arr=res.data.create.concat(res.data.join);
        localStorage.setItem('groupList',JSON.stringify(arr));
      })
    }
  }



