import { Message,Agree,Addgroup } from '@/api/index'
export default {
  data () {
    return {
      list:[],
      headName:'群通知'
    }
  },
  methods: {
    gainMessage() {
       Message({}).then( res => {
          console.log(res)
          if(res.code == 1) {
             this.list = res.data
          }
       })
    },
    joinG(val) {
      this.loading()
      Agree({groId:val.groupId,apply:val.send}).then(res => {
        this.hideLoading()
         if(res.code == 1) {
            console.log(res)
            this.gainMessage()
         }
      })
    },
    // applyFn(id){//申请入群
    //   Addgroup({groId:id}).then( res => {
    //     if(res.code == 1 ) {
    //       this.$router.go(0)
    //     }
    //   })
    // }
  },
  created () {
    this.gainMessage()
  },
  components: {
  }
}



