import { groupsDetail,Addgroup } from '@/api/index'
import util from "@/util/util"
export default {
    name: 'applyEntry', // 结构名称
    data() {
        return {
            // 数据模型
            id:'',//群id
            name:'',//群名
            imgurl:'',//群头像
            num:'',//群人数
            applyTxt:'申请入群',
            mask:false,
            toastTxt:'您的申请已提交，请等待群组审批',
            showQrcode:false
        }
    },
    watch: {
        // 监控集合
    },
    computed:{
        // showQrcode(){
        //     if(this.$route.meta.showQrcode){
        //         return true
        //     }else{
        //         alert('f');
        //         return false
        //     }
        // }
    },
    props: {
        // 集成父级参数
    },
    beforeCreate() {
        // console.group('创建前状态  ===============》beforeCreate');
    },
    created() {
        // console.group('创建完毕状态===============》created');
    },
    beforeMount() {
        // console.group('挂载前状态  ===============》beforeMount');
    },
    mounted() {
        let that=this;
        this.id=this.$route.query.id;
        groupsDetail(this.id).then(res =>{
            that.imgurl=res.data.imgUrl;
            that.name=res.data.groName;
            that.num=res.data.userCount;
            if(res.data.isConcern==0){
                that.showQrcode=true;
            }
        })
        
        
        // console.group('挂载结束状态===============》mounted');
    },
    beforeUpdate() {
        // console.group('更新前状态  ===============》beforeUpdate');
    },
    updated() {
        // console.group('更新完成状态===============》updated');
    },
    beforeDestroy() {
        // console.group('销毁前状态  ===============》beforeDestroy');
    },
    destroyed() {
        // console.group('销毁完成状态===============》destroyed');
    },
    methods: {
        // 方法 集合
        applyGroup(){
            if(this.applyTxt!='申请入群'){
                return false
            }
            Addgroup({groId:this.id}).then( res => {
                if(res.code == 1 ) {
                    this.applyTxt='已申请';
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }else if(res.code==403){
                    this.applyTxt='已申请';
                    this.toastTxt=res.msg;
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }else{
                    this.applyTxt='已入群';
                    this.toastTxt=res.msg;
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }
            })
        },
        followFn(){//检测是否关注
            let appid=this.appid;
            let url=encodeURIComponent(location.href.split('?')[0].split('#')[0]+'?path=applyEntry&id='+this.id+'&istrue=1');
            util.weixinauth(appid,url);
        }   
    }

}




