export default {
  name: "popup", // 结构名称
  data() {
    return {
      // 数据模型
      text: ""
    };
  },
  watch: {
    // 监控集合
    tipText: function(newValue, oldValue) {
      this.text = newValue;
    }
  },
  props: {
    // 集成父级参数
    popupType: {
      type: String
    },
    popupShow: {
      default: false
    },
    tipText: {
      default: "是否继续该操作？"
    },
    sureTxt: {
      default: "确定"
    },
    cancelTxt: {
      default: "取消"
    },
    message: {
    }
  },
  computed: {
    txt() {
      return this.tipText;
    }
  },
  beforeCreate() {
    // console.group('创建前状态  ===============》beforeCreate');
  },
  created() {
    // console.group('创建完毕状态===============》created');
  },
  mounted() {
    // console.group('挂载结束状态===============》mounted');
    this.$nextTick(function() {
      // console.log('执行完后，执行===============》mounted');
    });
  },
  methods: {
    // 方法 集合
    cancelFn() {
      //取消
      this.$parent.mask = false;
      this.$emit("quxiao", { data: false });
    },
    sure() {
      this.$parent.mask = false;
      this.$emit("sure", { data: true });
    }
  }
};



