import { User, Friends, Parise, groups } from "@/api/index";
export default {
  data() {
    return {
      name: "",
      imgUrl: "",
      compang: "",
      pushStart: "",
      pushEnd: "",
      total: "",
      time: "",
      que: "",
      pushd: "",
      pushDays: "",
      list: [],
      isScroll: true,
      noGet: false,
      pageList: { total: "", pageNo: 1, pageSize: 4 }
    };
  },
  watch: {
    //监控集合
    $route: function(route) {
      // this.$router.go(0);
      location.reload();
    }
  },
  // components:{
  //   'clock':clocklist
  // },
  methods: {
    jumpF() {
      let id = '';
      if (this.$route.query.id) {
        id = this.$route.query.id;
      }else {
        id = localStorage.getItem("userid");
      }
      this.$router.push({
        path: "/nav/friend",
        query: {
          id: id
        }
      });
    },
    gainUser(userid) {
      let data = {};
      if (userid) {
        data.userId = userid;
      }
      User(data).then(res => {
        this.hideLoading();
        this.name = res.data.name;
        this.imgUrl = res.data.imgUrl;
        this.compang = res.data.company;
        this.pushStart = res.data.pushStart;
        this.pushEnd = res.data.pushEnd;
        this.total = res.data.total;
        this.time = res.data.pushDays;
        this.que = res.data.lostCount;
      });
    },
    gainList(userid) {
      let that = this;
      let data = {
        pageNo: that.pageList.pageNo,
        pageSize: that.pageList.pageSize
      };
      if (userid) {
        data.userId = userid;
      }
      Friends(data).then(res => {
        //  that.list = res.data.list;
        that.isScroll = true;
        if (res) {
          if (that.pageList.pageNo > 1) {
            that.list = that.list.concat(res.data.list);
            that.pageList.total = res.data.total;
          } else {
            that.list = res.data.list;
            that.pageList.total = res.data.total;
          }
          if (
            res.data.total == that.list.length ||
            that.list.length < that.pageList.pageSize
          ) {
            that.noGet = true; //如果返回总条数等于当前list长度
          }
        }
        res.data.list.forEach(el => {
          el.isFull = false;
          el.fullTxt = "全文";
        });
      });
    },
    sh() {
      let bodyScrollHeight = 0;
      let documentScrollHeight = 0;
      if (document.body) {
        bodyScrollHeight = document.body.scrollHeight;
      }
      if (document.documentElement) {
        documentScrollHeight = document.documentElement.scrollHeight;
      }
      return bodyScrollHeight - documentScrollHeight > 0
        ? bodyScrollHeight
        : documentScrollHeight;
    },
    //监听滚轮
    scrollgun() {
      let getSt = document.documentElement.scrollTop || document.body.scrollTop,
        getWh =
          document.documentElement.clientHeight || document.body.clientHeight;
      let scrollBottom = this.sh() - (getSt + getWh), //滚动条距离底部距离
        h = 100;
      if (this.noGet) {
        return false;
      }
      if (!this.isScroll) {
        return false;
      }
      if (scrollBottom < h) {
        this.isScroll = false;
        this.pageList.pageNo += 1;
        if (this.$route.query.id) {
          this.gainList(this.$route.query.id);
        } else {
          this.gainList();
        }
      }
    }
  },
  created() {
    this.loading();
    if (this.$route.query.id) {
      let id = this.$route.query.id;
      this.gainUser(id);
      this.gainList(id);
    } else {
      this.gainUser();
      this.gainList();
    }
  },
  components: {},
  mounted() {
    window.addEventListener("scroll", this.scrollgun, true);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollgun, true);
    // console.group('销毁完成状态===============》destroyed');
  }
};



