import { setInterval, clearInterval, setTimeout } from "timers";
export default {
  name: "audioPlay",
  data() {
    return {
      isClick: false,
      counts: {
        m: "00",
        s: "00"
      },
      num: {
        m: "00",
        s: "00"
      },
      maxTime: 0,
      nowTime: 0,
      slider: 0,
      t: null,
      mask: false,
      tipTxt: "确认删除该录音吗?",
      isRead: false
    };
  },
  props: {
    audioPath: {
      default: ""
    },
    isCard: {
      default: 0
    },
    time: {
      type: String
    }
  },
  methods: {
    timer() {
      const that = this;
      let second = that.counts.s;
      let minute = that.counts.m;
      this.nowTime++;
      if (this.nowTime > this.maxTime) {
        //双保险
        return false;
      }
      second++;
      this.slider = parseInt((this.nowTime / this.maxTime) * 100);
      if (second >= 60) {
        second = 0; //  大于等于60秒归零
        minute++;
        // if (minute >= 60) {
        //     minute = 0  //  大于等于60分归零
        // }
        if (minute < 10) {
          // 少于10补零
          that.counts.m = "0" + minute;
          that.counts.s = second;
        } else {
          that.counts.m = minute;
          that.counts.s = second;
        }
      }
      if (second < 10) {
        // 少于10补零
        that.counts.m = minute;
        that.counts.s = "0" + second;
      } else {
        that.counts.m = minute;
        that.counts.s = second;
      }
    },
    readPlay() {
      //准备播放
      if (this.$refs.test.duration) {
        alert('this.$refs.test.duration',this.$refs.test.duration);
        const maxTime = parseInt(this.$refs.test.duration);
        this.maxTime = maxTime;
        this.num.m = parseInt(maxTime / 60);
        this.num.s = maxTime % 60;
        if (this.num.m < 10) {
          this.num.m = "0" + this.num.m;
        }
        if (this.num.s < 10) {
          this.num.s = "0" + this.num.s;
        }
        this.isRead = true;
      }
    },
    playingFn() {
      //开始播放的时候
      this.t = setInterval(this.timer, 1000);
    },
    stop() {
      //模拟停止
      let audio = this.$refs.test;
      audio.pause();
      audio.currentTime = 0;
      this.audioEnd();
    },
    pause() {
      //暂停
      let audio = this.$refs.test;
      audio.pause();
      clearInterval(this.t);
      this.isClick = false;
    },
    playFn() {
      let audio = this.$refs.test;
      if (this.isClick) {
        //暂停
        audio.pause();
        clearInterval(this.t);
      } else {
        this.$emit("audioPlay");
        audio.play();
      }
      this.isClick = !this.isClick;
    },
    sliderChange(value) {
      //进度条change
      clearInterval(this.t);
      let audio = this.$refs.test;
      this.slider = value;
      this.nowTime = parseInt((this.maxTime * value) / 100);
      this.counts.m = parseInt(this.nowTime / 60);
      this.counts.s = this.nowTime % 60;
      if (this.counts.m < 10) {
        this.counts.m = "0" + this.counts.m;
      }
      if (this.counts.s < 10) {
        this.counts.s = "0" + this.counts.s;
      }
      audio.currentTime = this.nowTime;
      this.t = setInterval(this.timer, 1000);
    },
    deleteAudio() {
      this.$emit("deAudio");
    },
    tapDel() {
      this.mask = true;
    },
    audioEnd() {
      //播放完毕停止计时
      clearInterval(this.t);
      this.counts = {
        m: "00",
        s: "00"
      };
      this.t = null;
      this.nowTime = 0;
      this.slider = 0;
      this.isClick = false;
    }
  },
  created() {
    if(!this.$parent.recordTime == ''){
      alert('this.$parent.recordTime',this.$parent.recordTime);
    }
  },
  mounted() {
    let u = navigator.userAgent;
    let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
    if (isiOS) {
      //IOS不会自动触发canplay事件。
      this.isRead = true;
    }
  }
};



