export default {
    name: "headTitle",
    props:{
      headTxt:{
        type:String
      },
      headType:{
        default:'goTo'
      }
    },
  methods:{
      jump(){
        localStorage.setItem('tabNum','3')
        this.$router.go(-1);
      },
      jumpQrCode(){
        this.$emit('jump',{})
      }
    }

}



