
import { Personage, Saveuser } from "@/api/index";
import dateFormat from 'vux/src/tools/date/format.js'

import { setTimeout } from "timers";
import { Stepper } from 'vant';
export default {
inject: ["reload"],
data() {
return {
user: [],
bookss: [],
books: [],
values: "",
Blength: 0,
pushCount: [],
Number: true,
delay: true,
success: false,
readNum: false,
newbook: [],
text2: false,
text3: false
};
},
methods: {
bookChang(el) {
if (el.readCount == 0) {
el.type = 0;
}
},
bblur() {
window.scroll(0, 0);
},
cardDel(i) {
this.books.splice(i, 1);
},
cardBook() {
this.books.push({});
},
keeping() {
if (this.pass && this.delay) {
let that = this;
this.delay = false;
this.pushCount.volunteer = this.values;
this.pushCount.bonaStart = this.pushCount.bonaStart.replace("年", "-");
this.pushCount.bonaStart = this.pushCount.bonaStart.replace("月", "-");
this.pushCount.bonaStart = this.pushCount.bonaStart.replace("日", "");
this.newbook = this.bookss.concat(this.books);

localStorage.setItem("userName", this.user.name);
let local = localStorage.getItem("cardPushData");
if (local) {
let data = JSON.parse(local);
data.volunteer = this.values;
localStorage.setItem("cardPushData", JSON.stringify(data));
}
delete this.pushCount.bonaTotal;
Saveuser({
books: this.newbook,
user: this.user,
pushCount: this.pushCount
}).then(res => {
if (res.code == 1) {
this.success = true;
this.delay = true;
this.gainUser();
return setTimeout(() => {
this.success = false;
this.reload();
}, 1500);
}
});
}
},
record() {
this.pass = true;
if (!this.user.name) {
this.pass = false;
this.text2 = true;
return setTimeout(() => {
this.text2 = false;
}, 1500);
}
this.delay = true;
if (this.books[0]) {
for (let i in this.books) {
if (!this.books[i].title) {
this.text3 = true;
return setTimeout(() => {
this.text3 = false;
}, 1500);
break;
} else if (!this.books[i].readCount) {
this.readNum = true;
return setTimeout(() => {
this.readNum = false;
}, 1500);
break;
}
}
this.keeping();
} else {
this.keeping();
}
},
gainUser() {
Personage({}).then(res => {
if (res.code == 1) {
this.user = res.data.user;
this.bookss = res.data.books;
this.Blength = res.data.books.length + 1;
this.pushCount = res.data.pushCount;
this.values = res.data.volunteer;
}
});
}
},
created() {
this.gainUser();
},
components: {}
};



